/**
 * @since 2.15
 * @param formName
 * @param formInput
 * @returns {Promise}
 */


function submitForm(formName, formInput) {
  if (!_.isUndefined(formInput.callDate)) {
    try {
      formInput.callDate = moment(formInput.callDate).format('YYYY-MM-DD');
    } catch (e) {}
  }

  return new Promise(function (resolve, reject) {
    if (isMobileLocker()) {
      var formData = {
        method: 'capturedata',
        category: 'data-capture',
        action: formName,
        uri: formName,
        data: formInput
      };
      axios.post(ML_ENDPOINT, formData).then(function (response) {
        resolve(response.data);
      }).catch(function (err) {
        reject(err);
      });
    } else {
      logger.info('Faking form submission');
      setTimeout(function () {
        resolve(true);
      }, 3000);
    }
  });
}
