/**
 * @since 2.15
 * @param pdfPath
 * @param title
 * @param customOptions
 * @returns {boolean}
 */


function openPDF(pdfPath, title, customOptions) {
  if (isMobileLocker()) {
    var options = {
      'search': 'Y',
      //Y or N.
      'outline': 'Y',
      //Y or N
      'annotations': 'Y',
      //Y or N
      'vertical': 'N',
      //Y or N
      'toolbar': 'Y',
      //Y or N.  If N, the toolbar will not display (we highly recommend keeping this as Y)
      'scrolling': 'Y',
      //Y or N
      'maximumZoomScale': 5.0,
      //0.0 to 10.0
      'pageModeDouble': 'N',
      //Y or N: if Y, show 2 pages next to each other
      'statusBarStyleSetting': 5,
      //1, 2, 3, 4, 5
      'fitWidth': 'N',
      //Y or N - if Y, pages are fit to screen width, not to either height or width (which one is larger – usually height.) Defaults to NO. fitWidth is currently not supported for vertical scrolling. This is a know limitation of the PDF Rendering Library, not Mobile Locker.
      'viewMode': 1 //1=Document, 2=Thumbnails

    };

    _.merge(options, customOptions);

    var formData = {
      method: 'showpdf',
      category: 'PDF',
      action: 'Open',
      uri: pdfPath,
      data: {
        filename: pdfPath,
        title: title
      }
    };
    axios.post(ML_ENDPOINT, formData);
    return true;
  } else {
    window.open(pdfPath, '_blank');
  }
}
